import { useState } from 'react';
import './App.css';
import React from 'react'
// import About from './components/About';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import Alert from './components/Alert';
// import {
//   BrowserRouter as Router,
//   Switch,
//   Route,
// } from "react-router-dom";

function App() {
  const [mode, setMode] = useState('light');

  const [alert, setAlert] = useState(null);

 const showAlert = (message, type) => {
      setAlert({
        msg: message,
        type: type
      })

      setTimeout(() => {
        setAlert(null);
      }, 2000);
 }

  const toggleMode = (name) => {
 if (mode === 'light'){
   setMode('dark');
   document.body.style.background = "#383838"
   name.colorb="black"
   showAlert("Dark mode has been enabled", "success")
   document.title='TextUtils - Dark Mode';
 }
 else{
   setMode('light');
   document.body.style.background = "white"
   showAlert("Light mode has been enabled", "success")
   document.title='TextUtils - Light Mode';
 }}

 const toggleMode2 = (name) => {
  if (mode === 'light'){
    setMode('dark');
    document.body.style.background = "#827397"
    showAlert("Dark mode has been enabled", "success")
  }
  else{
    setMode('light');
    document.body.style.background = "white"
    showAlert("Light mode has been enabled", "success")
  }}

  const toggleMode3 = (name) => {
    if (mode === 'light'){
      setMode('dark');
      document.body.style.background = "#064663"
      showAlert("Dark mode has been enabled", "success")
    }
    else{
      setMode('light');
      document.body.style.background = "white"
      showAlert("Light mode has been enabled", "success")
    }}
  return (
    <>
    {/* <Router> */}
<Navbar title="TextUtils" aboutText="About" mode={mode} toggleMode={toggleMode} toggleMode2={toggleMode2} toggleMode3={toggleMode3} />
<Alert alert={alert}/>
<div className="container">
{/* <Switch> */}
      {/* <Route exact path="/about">
            <About />
        </Route> */}
          {/* <Route exact path="/" element> always use exact */}
          <TextForm  showAlert={showAlert} heading="Enter the Text" mode={mode}/> 
          {/* </Route> */}
          {/* <About/> */}
{/* </Switch> */}
</div>
{/* </Router> */}
  </>
  );
}

export default App;
